import { useEffect, useState } from "react";
import { GoThreeBars } from "react-icons/go";
import './Navbar.css';
 
const Navbar = ()=> {
    const [name, setName] = useState(false)
    const [hover , setHover] = useState(false)
        const handleScroll = () => {
          const scrollPosition = window.scrollY; // => scroll position
          let com= document.getElementById("navComp")
          if(scrollPosition > 20 ){
            setName(true)
            com.classList.add("mystyle")
          }
          else{
            setName(false)
            com.classList.remove("mystyle")
          }
      };
      useEffect(() => {
        handleScroll();
        window.addEventListener("scroll", handleScroll);
        return () => {
          window.removeEventListener("scroll", handleScroll);
        };
        }, []);
        console.log(hover)
    function openNav() {
        document.getElementById("myNav").style.height = "100%";
      }
      
      function closeNav() {
        document.getElementById("myNav").style.height = "0%";
      }

    return  <div style={{
        padding : name ? "15px 40px" : "" ,
        backgroundColor : name ? "#92bcb5" : "transparent",
        color : name ? "black" : ""

    }}  id="nav-menu">
    
   <a href="/"> <p style={{ color : name ? "black" : ""

}} id="navName" >Kartik Jain </p></a>
    <p id="navComp"  style={{ color : name ? "black" : ""
}} 
>
    <a className="nav-link home" href="/">Home</a>
    <a className="nav-link about" href="/About">About</a>
    <a className="nav-link skills" href="/Skills">Skills</a>
    <a className="nav-link projects" href="/Projects">Projects</a>
    <a className="nav-link contact" href="/Contact">Contact</a>
    <a className="nav-link resume" href="https://drive.google.com/file/d/19vD9-jQMpAhc3_5ZnVkNKzAw70IvYk71/view?usp=share_link" target="_blank" download >Resume</a>
    </p>
    <p id="mobile" onClick={openNav} style={{fontSize:"30px",color:"white", cursor: "pointer",float: "right", textAlign: "center"  }} ><GoThreeBars/></p>

   <div id="myNav" className="overlay">
  <a className="closebtn" onClick={closeNav}>&times;</a>
  <div className="overlay-content">
    <a href="/">HOME</a>
    <a href="/About">ABOUT</a>
    <a href="/Skills">SKILLS</a>
    <a href="/Projects">PROJECTS</a>
    <a href="/Contact">CONTACT</a>
    <a href="https://drive.google.com/file/d/19vD9-jQMpAhc3_5ZnVkNKzAw70IvYk71/view?usp=share_link" target="_blank" download >RESUME</a>
  </div>
</div>
  </div>
  
  
}

export default Navbar;